.promo-block {
  position: relative;
  padding-bottom: 50px;
}

.promo-block__title {
  margin-bottom: 44px;
}

.promo-block__button {
  width: 407px;
  margin-left: 115px;
  padding: 18px 0;
  border: 0;

  font-size: 16px;
  line-height: 19px;

  background-color: $basic-blue-color;
    &:hover,
    &:focus {
      background-color: #ffffff;
      color: $basic-blue-text;
    }
    &:active {
      background-color: #ffffff;
      color: $basic-blue-text;
      opacity: 0.6;
    }
}

.promo-block__note {
  position: absolute;
  bottom: -98px;
  left: -23px;

  display: block;
  border: 0;

  font-size: 14px;
  line-height: 16px;
  background-color: transparent;
  color: rgba(255, 255, 255, 0.5);

  transform: rotate(270deg);
  outline: none;
  cursor: pointer;
    &:hover,
    &:focus {
      color: $basic-white-text;
      transition: color 0.5s ease;
    }
    &:active {
      color: $basic-blue-text;
      opacity: 0.6;
      transition: color opacity 0.5s ease;
      transition: opacity 0.5s ease;
    }
}
